'use client';
import Link from 'next/link';
import { useAuth } from '@/contexts/AuthContext';

export default function Header() {
  const { user, signOut } = useAuth();

  return (
    <header className="bg-gradient-to-r from-blue-600 to-purple-700 shadow-lg">
      <div className="container mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center py-6">
          <Link href="/" className="flex items-center space-x-3 group">
            <div className="w-12 h-12 bg-white rounded-full flex items-center justify-center shadow-lg group-hover:shadow-xl transition-all duration-300 transform group-hover:scale-110">
              <svg className="w-8 h-8 text-blue-600" viewBox="0 0 32 32" fill="currentColor">
                <path d="M8 6h16v8H8z" fill="#F59E0B"/>
                <path d="M14 14h4v12h-4z" fill="#1F2937"/>
                <path d="M20 8h3v10h-3z" fill="#DC2626"/>
                <path d="M18 18h7v2h-7z" fill="#DC2626"/>
              </svg>
            </div>
            <div className="flex flex-col">
              <span className="text-2xl font-bold text-white group-hover:text-blue-100 transition-colors duration-300">AuctionHub</span>
              <span className="text-xs text-blue-200 font-medium">Real-Time Auctions</span>
            </div>
          </Link>
          
          <nav className="flex items-center gap-6">
            {user ? (
              <div className="flex items-center gap-4">
                <Link 
                  href="/create-auction"
                  className="bg-white/20 hover:bg-white/30 text-white font-semibold py-2 px-4 rounded-full transition-all duration-200 transform hover:scale-105 backdrop-blur-sm"
                >
                  Create Auction
                </Link>
                <div className="flex items-center space-x-3 bg-white/10 backdrop-blur-sm rounded-full px-4 py-2">
                  <div className="w-8 h-8 bg-white rounded-full flex items-center justify-center">
                    <span className="text-sm font-semibold text-blue-600">
                      {user.email?.charAt(0).toUpperCase()}
                    </span>
                  </div>
                  <span className="text-white text-sm font-medium hidden sm:block">
                    {user.email}
                  </span>
                </div>
                <button
                  onClick={signOut}
                  className="bg-red-500 hover:bg-red-600 text-white font-semibold py-2 px-6 rounded-full transition-all duration-200 transform hover:scale-105 shadow-lg hover:shadow-xl"
                >
                  Sign Out
                </button>
              </div>
            ) : (
              <Link 
                href="/login" 
                className="bg-white text-blue-600 font-semibold py-3 px-8 rounded-full hover:bg-gray-100 transition-all duration-200 transform hover:scale-105 shadow-lg hover:shadow-xl"
              >
                Get Started
              </Link>
            )}
          </nav>
        </div>
      </div>
    </header>
  );
}